	export default {
		data() {
			return {
				tabList,
				goodsList,
				colorItem,
				mixItem,
				recommList,
				showPickerAddr: false,
				current: 0,
				colorIndex: 0,
				mixIndex: 0,
				richHtml: '<p style=\"text-align: center;\"><img src=\"https://www.duobihouse.com/fileUEditor/upload/image/20210731/6376334413454683059410276.jpg\" title=\"R-B-9016.jpg\" alt=\"R-B-9016.jpg\"/></p>',
				tabInfoIndex: 0,
				scrollViewId: 'scroll0',
				scrollHeight: '600px',
				scrollTopInfo: {
					scroll0: 0,
					scroll1: 0,
					scroll2: 0,
				},
				selectTxt: {
					colorTxt: '湖水蓝',
					mixTxt: '单人位（0.8米）',
					numTxt: 1
				},
				addressTxt: {
					area: {
						value: '110101',
						label:'东城区'
					},
					city: {
						value: '1101',
						label:'市辖区'
					},
					province: {
						value: '11',
						label:'北京市'
					}
				}
			};
		},

		mounted() {
			//在富文本框内容渲染完成之后 获取元素距离顶部高度
			this.imgAddMaxWidth(this.richHtml).then(res => {
				this.richHtml = res
			}).then(() => {
				//获取 商品 + 详情 + 推荐 初始化时距离顶部的位置
				this.$nextTick(() => {
					// console.log(this.$refs.scroll1.$el.getBoundingClientRect())
					const query = uni.createSelectorQuery().in(this);
					for (let i = 0; i <= 2; i++) {
						query.select(`#scroll${i}`).boundingClientRect(data => {
							this.scrollTopInfo[`scroll${i}`] = data.top - 60
						}).exec();
					}
				})
			})


		},

		methods: {
			/*省市区选择*/
			onConfirm(e) {
                Object.assign(this.addressTxt,e)
				
			},
			onClickColor(color, index) {
				this.colorIndex = index
				this.selectTxt.colorTxt = color.name + ' , '
			},
			onClickMix(mix, index) {
				this.mixIndex = index
				this.selectTxt.mixTxt = mix.name + ' , '
			},
			openGoodsPopup() {
				this.$refs.popup.open('bottom')
			},
			cloneGoodsPopup() {
				this.$refs.popup.close()
			},
			onChangeTabs(e) {
				this.current = e
				this.scrollViewId = `scroll${e}`
			},
			onChangeInfo(i) {
				this.tabInfoIndex = i
			},
			imgAddMaxWidth(str) {
				return new Promise((reslove, reject) => {
					if (!str) {
						return "";
					}
					let regex = new RegExp("/>", "gi");
					let resStr = str.replace(regex, " style='max-width: 100%;'/>");
					reslove(resStr)

				})
			},
			onScroll(e) {
				const top = e.detail.scrollTop
				const {
					scroll0,
					scroll1,
					scroll2
				} = this.scrollTopInfo
				if (top >= scroll0) {
					this.current = 0
				}
				if (top >= scroll1) {
					this.current = 1
				}
				if (top >= scroll2) {
					this.current = 2
				}
				console.log('位置打印', top, scroll0, scroll1, scroll2)


			},
			onBuy() {
				uni.showToast({
					title: '点击了立即购买'
				})
			},
			onAddCart() {
				uni.showToast({
					title: '添加成功',
					success: () => {
						this.$refs.popup.close()
					}
				})

			}

		},

	}
